/*
NOTICE: It's better to use the modern CSS variable.
        However, the mobile side is fragile on supporting that.
        So we go with SCSS variable and function.
*/
.App {
  width: 100%;
  max-width: 100%;
  height: 100%;
  margin: 0 auto;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  background: #fff; }

@media screen and (min-width: 768px) {
  .App {
    width: 70%;
    max-width: 640px; } }

.todo-panel {
  width: 100%; }

.todo-panel__top-panel {
  width: 100%;
  max-width: 100%;
  height: 9rem;
  padding-bottom: 1rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: fixed;
  top: 0;
  background: #51c87a; }

.todo-panel__title {
  height: 5rem;
  line-height: 5rem;
  margin: 0;
  font-size: 2.4rem;
  font-weight: lighter;
  text-align: center;
  color: #fafbfc; }

.todo-panel__input {
  width: 80%;
  height: 3rem;
  padding: 0.5rem 1rem;
  border: 0;
  border-bottom: 1px solid #efefef;
  margin: 0 auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 1.1rem;
  display: block;
  color: #555; }

/* Grid/Flexbox Layout:
   Easier to achieve centering, positioning elements with the modern layout rules. 
   However, the mobile side is fragile on supporting that.
   So we still use the old ways above and put the modern ways here.
*/
.todo-panel__top-panel {
  display: grid;
  grid-template-rows: 5fr 3fr;
  grid-template-columns: 1fr; }

.todo-panel__title {
  grid-area: 1 / 1 / 1 / 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center; }

.todo-panel__input {
  grid-area: 2 / 1 / 2 / 1; }

/* Grid/Flexbox Layout End*/
@media screen and (min-width: 768px) {
  .todo-panel__top-panel {
    width: 70%;
    max-width: 640px; } }



